"use client";

import { useEffect, useMemo, useState } from "react";
import Image from "next/image";

import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";

import {
  Product,
  addProduct,
  deleteProduct,
  loadProducts,
  seedIfEmpty,
  exportProductsCSV,
  importProductsCSV,
} from "@/lib/store";

// ✅ ย่อรูป + บีบอัดอัตโนมัติ (รองรับรูปใหญ่)
async function resizeImageToBase64(file: File, maxSize = 900): Promise<string> {
  const img = document.createElement("img");
  const url = URL.createObjectURL(file);

  await new Promise<void>((resolve, reject) => {
    img.onload = () => resolve();
    img.onerror = () => reject();
    img.src = url;
  });

  const ratio = Math.min(maxSize / img.width, maxSize / img.height, 1);
  const w = Math.round(img.width * ratio);
  const h = Math.round(img.height * ratio);

  const canvas = document.createElement("canvas");
  canvas.width = w;
  canvas.height = h;

  const ctx = canvas.getContext("2d");
  if (!ctx) throw new Error("Canvas not supported");

  ctx.drawImage(img, 0, 0, w, h);

  URL.revokeObjectURL(url);

  // ✅ บีบเป็น jpeg (ช่วยลดขนาด localStorage)
  return canvas.toDataURL("image/jpeg", 0.8);
}

function downloadCSV(filename: string, content: string) {
  const BOM = "\uFEFF"; // ✅ ทำให้ Excel อ่านภาษาไทยถูก
  const blob = new Blob([BOM + content], { type: "text/csv;charset=utf-8" });
  const url = URL.createObjectURL(blob);
  const a = document.createElement("a");
  a.href = url;
  a.download = filename;
  a.click();
  URL.revokeObjectURL(url);
}

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [open, setOpen] = useState(false);

  const [barcode, setBarcode] = useState("");
  const [sku, setSku] = useState("");
  const [name, setName] = useState("");
  const [category, setCategory] = useState("ทั่วไป");
  const [unit, setUnit] = useState("ชิ้น");
  const [cost, setCost] = useState<number>(0);
  const [price, setPrice] = useState<number>(0);
  const [minStock, setMinStock] = useState<number>(0);
  const [stock, setStock] = useState<number>(0);
  const [image, setImage] = useState<string | undefined>(undefined);

  useEffect(() => {
    seedIfEmpty();
    setProducts(loadProducts());
  }, []);

  const refresh = () => setProducts(loadProducts());

  const canSave = useMemo(() => {
    return sku.trim() && name.trim() && price >= 0 && cost >= 0;
  }, [sku, name, price, cost]);

  const onAdd = () => {
    if (!canSave) return;

    addProduct({
      barcode: barcode.trim() || undefined,
      sku: sku.trim(),
      name: name.trim(),
      category: category.trim() || "ทั่วไป",
      unit: unit.trim() || "ชิ้น",
      cost: Number(cost) || 0,
      price: Number(price) || 0,
      minStock: Number(minStock) || 0,
      stock: Number(stock) || 0,
      image,
      isActive: true,
    });

    setBarcode("");
    setSku("");
    setName("");
    setCategory("ทั่วไป");
    setUnit("ชิ้น");
    setCost(0);
    setPrice(0);
    setMinStock(0);
    setStock(0);
    setImage(undefined);

    setOpen(false);
    refresh();
  };

  return (
    <div className="space-y-6">
      {/* ✅ HEADER */}
      <div className="flex items-start justify-between gap-4">
        <div>
          <h1 className="text-2xl font-bold">สินค้า (Mini-Mart)</h1>
          <p className="text-sm text-slate-500">
            เพิ่มสินค้า พร้อมราคา/ต้นทุน/บาร์โค้ด/รูป + Import/Export CSV
          </p>
        </div>

        {/* ✅ TOP BUTTONS */}
        <div className="flex flex-wrap items-center gap-2">
          <Button
            variant="secondary"
            onClick={() => {
              const csv = exportProductsCSV();
              downloadCSV(`products_${Date.now()}.csv`, csv);
            }}
          >
            Export Products CSV
          </Button>

          <label className="inline-flex cursor-pointer items-center rounded-xl border bg-white px-3 py-2 text-sm">
            Import CSV
            <input
              type="file"
              accept=".csv"
              className="hidden"
              onChange={async (e) => {
                const f = e.target.files?.[0];
                if (!f) return;

                const text = await f.text();
                importProductsCSV(text);

                alert("Import สำเร็จ ✅");
                refresh();
              }}
            />
          </label>

          <Dialog open={open} onOpenChange={setOpen}>
            <DialogTrigger asChild>
              <Button className="rounded-xl">+ เพิ่มสินค้า</Button>
            </DialogTrigger>

            <DialogContent className="rounded-2xl">
              <DialogHeader>
                <DialogTitle>เพิ่มสินค้า</DialogTitle>
              </DialogHeader>

              <div className="space-y-3">
                <div>
                  <div className="text-sm font-medium">Barcode</div>
                  <Input
                    value={barcode}
                    onChange={(e) => setBarcode(e.target.value)}
                    placeholder="เช่น 8850001112223"
                    className="mt-2"
                  />
                </div>

                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <div className="text-sm font-medium">SKU</div>
                    <Input
                      value={sku}
                      onChange={(e) => setSku(e.target.value)}
                      className="mt-2"
                    />
                  </div>
                  <div>
                    <div className="text-sm font-medium">หมวดหมู่</div>
                    <Input
                      value={category}
                      onChange={(e) => setCategory(e.target.value)}
                      className="mt-2"
                    />
                  </div>
                </div>

                <div>
                  <div className="text-sm font-medium">ชื่อสินค้า</div>
                  <Input
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="mt-2"
                  />
                </div>

                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <div className="text-sm font-medium">หน่วย</div>
                    <Input
                      value={unit}
                      onChange={(e) => setUnit(e.target.value)}
                      className="mt-2"
                    />
                  </div>
                  <div>
                    <div className="text-sm font-medium">Stock เริ่มต้น</div>
                    <Input
                      type="number"
                      value={stock}
                      onChange={(e) => setStock(Number(e.target.value))}
                      className="mt-2"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <div className="text-sm font-medium">ต้นทุน</div>
                    <Input
                      type="number"
                      value={cost}
                      onChange={(e) => setCost(Number(e.target.value))}
                      className="mt-2"
                    />
                  </div>
                  <div>
                    <div className="text-sm font-medium">ราคาขาย</div>
                    <Input
                      type="number"
                      value={price}
                      onChange={(e) => setPrice(Number(e.target.value))}
                      className="mt-2"
                    />
                  </div>
                </div>

                <div>
                  <div className="text-sm font-medium">ขั้นต่ำ</div>
                  <Input
                    type="number"
                    value={minStock}
                    onChange={(e) => setMinStock(Number(e.target.value))}
                    className="mt-2"
                  />
                </div>

                {/* ✅ UPLOAD IMAGE */}
                <div>
                  <div className="text-sm font-medium">รูปสินค้า</div>
                  <input
                    type="file"
                    accept="image/*"
                    className="mt-2 block w-full text-sm"
                    onChange={async (e) => {
                      const file = e.target.files?.[0];
                      if (!file) return;

                      // ✅ ไม่จำกัดขนาดแล้ว → ย่อ/บีบอัดให้เอง
                      const base64 = await resizeImageToBase64(file, 900);
                      setImage(base64);
                    }}
                  />

                  {image && (
                    <div className="mt-3 flex items-center gap-3">
                      <div className="relative h-16 w-16 overflow-hidden rounded-xl border bg-white">
                        <Image
                          src={image}
                          alt="preview"
                          fill
                          className="object-contain p-2"
                        />
                      </div>
                      <Button variant="secondary" onClick={() => setImage(undefined)}>
                        ลบรูป
                      </Button>
                    </div>
                  )}
                </div>

                <Button className="w-full rounded-xl" onClick={onAdd} disabled={!canSave}>
                  บันทึก
                </Button>
              </div>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {/* ✅ LIST */}
      <Card className="rounded-2xl">
        <CardContent className="p-4 space-y-3">
          {products.map((p) => {
            const low = p.stock <= p.minStock;
            return (
              <div
                key={p.id}
                className="flex items-center justify-between rounded-2xl border bg-white p-3"
              >
                <div className="flex items-center gap-3">
                  <div className="relative h-12 w-12 overflow-hidden rounded-xl border bg-white">
                    {p.image ? (
                      <Image
                        src={p.image}
                        alt={p.name}
                        fill
                        className="object-contain p-1"
                      />
                    ) : (
                      <div className="flex h-full w-full items-center justify-center text-[10px] text-slate-400">
                        No image
                      </div>
                    )}
                  </div>

                  <div>
                    <div className="font-semibold">{p.name}</div>
                    <div className="text-xs text-slate-500">
                      SKU: {p.sku} {p.barcode ? `| Barcode: ${p.barcode}` : ""}
                    </div>
                    <div className="text-xs text-slate-500">
                      ราคา {p.price} | ต้นทุน {p.cost} | หมวด {p.category}
                    </div>
                  </div>
                </div>

                <div className="flex items-center gap-3">
                  {low ? (
                    <Badge variant="destructive">ใกล้หมด</Badge>
                  ) : (
                    <Badge variant="secondary">ปกติ</Badge>
                  )}

                  <div className="text-right">
                    <div className="text-sm font-bold">
                      {p.stock} {p.unit}
                    </div>
                    <div className="text-xs text-slate-500">ขั้นต่ำ {p.minStock}</div>
                  </div>

                  <Button
                    variant="destructive"
                    onClick={() => {
                      if (!confirm(`ลบสินค้า: ${p.name}?`)) return;
                      deleteProduct(p.id);
                      refresh();
                    }}
                  >
                    ลบ
                  </Button>
                </div>
              </div>
            );
          })}

          {products.length === 0 && (
            <div className="rounded-2xl border bg-white p-6 text-center text-sm text-slate-500">
              ยังไม่มีสินค้า
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
