export type GRN = {
  id: string;
  docNo: string; // GRN-2026-0001
  supplierName?: string;
  createdAt: number;
  note?: string;
  items: GRNItem[];
  totalCost: number;
};

export type GRNItem = {
  productId: string;
  name: string;
  qty: number;
  cost: number;
  lineTotal: number;
};

export type AdjustmentReason =
  | "DAMAGED"
  | "LOST"
  | "EXPIRED"
  | "COUNT_DIFF"
  | "SYSTEM_FIX"
  | "OTHER";

export type AdjustmentDoc = {
  id: string;
  createdAt: number;
  reason: AdjustmentReason;
  note?: string;
  items: AdjustmentItem[];
};

export type AdjustmentItem = {
  productId: string;
  name: string;
  beforeStock: number;
  afterStock: number;
  diff: number;
};

export type CountSession = {
  id: string;
  docNo: string; // COUNT-2026-0001
  createdAt: number;
  status: "DRAFT" | "POSTED";
  note?: string;
  rows: CountRow[];
};

export type CountRow = {
  productId: string;
  name: string;
  systemStock: number;
  countedStock: number;
  diff: number;
};

const KEY_GRNS = "mm_grn_v1";
const KEY_ADJUST_DOCS = "mm_adjust_docs_v1";
const KEY_COUNTS = "mm_count_sessions_v1";
export function loadGRNs(): GRN[] {
  if (typeof window === "undefined") return [];
  return safeParse<GRN[]>(localStorage.getItem(KEY_GRNS), []);
}
export function saveGRNs(rows: GRN[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_GRNS, JSON.stringify(rows));
}

export function loadAdjustmentDocs(): AdjustmentDoc[] {
  if (typeof window === "undefined") return [];
  return safeParse<AdjustmentDoc[]>(localStorage.getItem(KEY_ADJUST_DOCS), []);
}
export function saveAdjustmentDocs(rows: AdjustmentDoc[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_ADJUST_DOCS, JSON.stringify(rows));
}

export function loadCountSessions(): CountSession[] {
  if (typeof window === "undefined") return [];
  return safeParse<CountSession[]>(localStorage.getItem(KEY_COUNTS), []);
}
export function saveCountSessions(rows: CountSession[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_COUNTS, JSON.stringify(rows));
}
function pad4(n: number) {
  return String(n).padStart(4, "0");
}

function nextDocNo(prefix: string, existing: { docNo: string }[]) {
  const year = new Date().getFullYear();
  const start = `${prefix}-${year}-`;
  const nums = existing
    .filter((x) => x.docNo.startsWith(start))
    .map((x) => Number(x.docNo.replace(start, "")))
    .filter((n) => Number.isFinite(n));
  const next = (nums.length ? Math.max(...nums) : 0) + 1;
  return `${prefix}-${year}-${pad4(next)}`;
}
export function createGRN(args: {
  supplierName?: string;
  note?: string;
  items: { productId: string; qty: number; cost: number }[];
}) {
  const products = loadProducts();
  const grns = loadGRNs();

  const docNo = nextDocNo("GRN", grns);
  const id = crypto.randomUUID();

  let totalCost = 0;

  const grnItems: GRNItem[] = args.items.map((it) => {
    const p = products.find((x) => x.id === it.productId);
    if (!p) throw new Error("Product not found");

    const lineTotal = it.qty * it.cost;
    totalCost += lineTotal;

    return {
      productId: it.productId,
      name: p.name,
      qty: it.qty,
      cost: it.cost,
      lineTotal,
    };
  });

  // update stock + ledger
  const ledger = loadLedger();
  for (const it of grnItems) {
    const idx = products.findIndex((p) => p.id === it.productId);
    const before = products[idx].stock;
    const after = before + it.qty;

    // update stock
    products[idx] = { ...products[idx], stock: after, cost: it.cost }; // update cost ล่าสุด

    ledger.unshift({
      id: crypto.randomUUID(),
      productId: it.productId,
      type: "PURCHASE_IN",
      qty: it.qty,
      beforeStock: before,
      afterStock: after,
      refType: "PO",
      refId: id,
      at: Date.now(),
    });
  }

  saveProducts(products);
  saveLedger(ledger);

  const grn: GRN = {
    id,
    docNo,
    supplierName: args.supplierName,
    createdAt: Date.now(),
    note: args.note,
    items: grnItems,
    totalCost,
  };

  grns.unshift(grn);
  saveGRNs(grns);

  return grn;
}
export function createAdjustment(args: {
  reason: AdjustmentReason;
  note?: string;
  items: { productId: string; afterStock: number }[];
}) {
  const products = loadProducts();
  const ledger = loadLedger();

  const doc: AdjustmentDoc = {
    id: crypto.randomUUID(),
    createdAt: Date.now(),
    reason: args.reason,
    note: args.note,
    items: [],
  };

  for (const it of args.items) {
    const idx = products.findIndex((p) => p.id === it.productId);
    if (idx === -1) continue;

    const before = products[idx].stock;
    const after = Math.max(0, it.afterStock);
    const diff = after - before;

    products[idx] = { ...products[idx], stock: after };

    doc.items.push({
      productId: it.productId,
      name: products[idx].name,
      beforeStock: before,
      afterStock: after,
      diff,
    });

    if (diff !== 0) {
      ledger.unshift({
        id: crypto.randomUUID(),
        productId: it.productId,
        type: "ADJUST",
        qty: Math.abs(diff),
        beforeStock: before,
        afterStock: after,
        refType: "ADJUST",
        refId: doc.id,
        at: Date.now(),
      });
    }
  }

  saveProducts(products);
  saveLedger(ledger);

  const docs = loadAdjustmentDocs();
  docs.unshift(doc);
  saveAdjustmentDocs(docs);

  return doc;
}
export function createCountSession(note?: string) {
  const sessions = loadCountSessions();
  const docNo = nextDocNo("COUNT", sessions);

  const products = loadProducts().filter((p) => p.isActive);

  const session: CountSession = {
    id: crypto.randomUUID(),
    docNo,
    createdAt: Date.now(),
    status: "DRAFT",
    note,
    rows: products.map((p) => ({
      productId: p.id,
      name: p.name,
      systemStock: p.stock,
      countedStock: p.stock,
      diff: 0,
    })),
  };

  sessions.unshift(session);
  saveCountSessions(sessions);

  return session;
}

export function updateCountRow(sessionId: string, productId: string, countedStock: number) {
  const sessions = loadCountSessions();
  const idx = sessions.findIndex((s) => s.id === sessionId);
  if (idx === -1) return;

  const s = sessions[idx];
  if (s.status !== "DRAFT") return;

  s.rows = s.rows.map((r) => {
    if (r.productId !== productId) return r;
    const counted = Math.max(0, countedStock);
    const diff = counted - r.systemStock;
    return { ...r, countedStock: counted, diff };
  });

  sessions[idx] = s;
  saveCountSessions(sessions);
}

export function postCountSession(sessionId: string) {
  const sessions = loadCountSessions();
  const idx = sessions.findIndex((s) => s.id === sessionId);
  if (idx === -1) throw new Error("Count session not found");

  const session = sessions[idx];
  if (session.status !== "DRAFT") throw new Error("Already posted");

  // ทำ adjustment ตาม diff
  const adjustmentItems = session.rows
    .filter((r) => r.diff !== 0)
    .map((r) => ({
      productId: r.productId,
      afterStock: r.countedStock,
    }));

  if (adjustmentItems.length > 0) {
    createAdjustment({
      reason: "COUNT_DIFF",
      note: `Cycle Count ${session.docNo}`,
      items: adjustmentItems,
    });
  }

  session.status = "POSTED";
  sessions[idx] = session;
  saveCountSessions(sessions);

  return session;
}
export function exportProductsCSV() {
  const rows = loadProducts();
  const header = [
    "sku",
    "barcode",
    "name",
    "category",
    "unit",
    "cost",
    "price",
    "stock",
    "minStock",
    "isActive",
  ].join(",");

  const lines = [header];

  for (const p of rows) {
    lines.push(
      [
        p.sku,
        p.barcode ?? "",
        `"${p.name.replace(/"/g, '""')}"`,
        p.category ?? "",
        p.unit,
        p.cost,
        p.price,
        p.stock,
        p.minStock,
        p.isActive ? "1" : "0",
      ].join(",")
    );
  }
  return lines.join("\n");
}

export function importProductsCSV(csv: string) {
  const lines = csv.split(/\r?\n/).filter((x) => x.trim());
  if (lines.length <= 1) return;

  const [header, ...rows] = lines;
  const cols = header.split(",").map((x) => x.trim());

  const get = (arr: string[], key: string) => {
    const i = cols.indexOf(key);
    return i >= 0 ? (arr[i] ?? "").trim() : "";
  };

  const products = loadProducts();

  for (const line of rows) {
    const arr = line.split(","); // NOTE: csv แบบง่าย ไม่รองรับ comma ในชื่อ (เรา quote ชื่อไว้แล้ว)

    const sku = get(arr, "sku");
    const name = get(arr, "name").replace(/^"|"$/g, "");
    if (!sku || !name) continue;

    const barcode = get(arr, "barcode") || undefined;

    const next: Product = {
      id: crypto.randomUUID(),
      sku,
      barcode,
      name,
      category: get(arr, "category") || "ทั่วไป",
      unit: get(arr, "unit") || "ชิ้น",
      cost: Number(get(arr, "cost") || 0),
      price: Number(get(arr, "price") || 0),
      stock: Number(get(arr, "stock") || 0),
      minStock: Number(get(arr, "minStock") || 0),
      image: undefined,
      isActive: get(arr, "isActive") !== "0",
    };

    products.unshift(next);
  }

  saveProducts(products);
}
export function getStockValuation() {
  const products = loadProducts().filter((p) => p.isActive);

  const rows = products.map((p) => ({
    id: p.id,
    name: p.name,
    sku: p.sku,
    stock: p.stock,
    cost: p.cost,
    value: p.stock * p.cost,
  }));

  const totalValue = rows.reduce((sum, r) => sum + r.value, 0);

  return { rows, totalValue };
}
